import React, {useState} from 'react'
import registerServices from '../../services/register'

const Register = () => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [repeatPassword, setRepeatPassword] = useState('')
  const [email, setEmail] = useState('')

  const handleUsernameChange = (event) => {
    setUsername(event.target.value)
  }

  const handlePasswordChange = (event) => {
    setPassword(event.target.value)
  }

  const handleRepeatPasswordChange = (event) => {
    setRepeatPassword(event.target.value)
  }

  const handleEmailChange = (event) => {
    setEmail(event.target.value)
  }

  const submitNewUser = (event) => {
    event.preventDefault()

    if(username.length === 0 || password.length === 0 || email.length === 0) {
      alert('Make sure you\'ve filled in all of the fields')
    } else if (password !== repeatPassword) {
      alert('Your passwords don\'t match')
    } else {
      const newUserObject = {
        username: username,
        password: password,
        email: email,
      }
      registerServices
        .register(newUserObject)
        .then(() => {
          setEmail('')
          setUsername('')
          setPassword('')
          setRepeatPassword('')
          
        })
        console.log(newUserObject)
    }

  }

  return (
    <div>
      <h2>Register</h2>
      <form onSubmit={submitNewUser}>
        <label>Username:</label>
        <input type='text' onChange={handleUsernameChange} /><br />
        <label>Password:</label>
        <input type='password' onChange={handlePasswordChange} /><br />
        <label>Repeat Password:</label>
        <input type='password' onChange={handleRepeatPasswordChange} /><br />
        <label>Email:</label>
        <input type="email" onChange={handleEmailChange} /><br />
        <input type='submit' value='Register'/>
      </form>
    </div>
  )
}

export default Register